import styled from '@emotion/styled'
import { Alert, Badge, Button, IconButton } from '@mui/material'
import DataGridDemo from 'components/DataGridAllProducts'
import { data } from 'variables/data'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import { useEffect, useState } from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import { useNavigate } from 'react-router-dom'
import AlerCostum from 'components/AlerCostum'
import { lsLoad, lsSave } from 'utils/utils'
import DataGridAllProducts from 'components/DataGridAllProducts'
import ProductsList from 'components/ProductsList'

export default function AllProducts() {
  const [selected, setSelected] = useState([])
  const [values, setValues] = useState(data)
  const [element, setElement] = useState()
  const navigate = useNavigate()

  const hundle = (value) => {
    const newSelected = [value, ...selected]
    setValues(values.filter((e) => !newSelected.includes(e)))
    setSelected(newSelected)
    setElement(value)
  }

  useEffect(() => {
    const storedSlected = lsLoad()
    setSelected(storedSlected)
    setValues(
      values.filter((e) => storedSlected.every((y) => y.Product != e.Product))
    )
  }, [])

  useEffect(() => {
    lsSave(selected)
  }, [selected])

  return (
    <div style={{ padding: 30 }}>
      <div style={{ display: `flex`, justifyContent: `flex-end` }}>
        <AlerCostum element={element} />
        <IconButton onClick={() => navigate(`/ShoppingCart`)}>
          <StyledBadge badgeContent={selected.length} color="secondary">
            <ShoppingCartIcon />
          </StyledBadge>
        </IconButton>
      </div>

      {/* <div>
        <DataGridAllProducts data={values} hundle={hundle} />
      </div> */}
      <ProductsList products={data} />
    </div>
  )
}
const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    right: -3,
    top: 13,
    border: `2px solid ${theme.palette.background.paper}`,
    padding: '0 4px',
  },
}))
