import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemAvatar from '@mui/material/ListItemAvatar'
import ListItemText from '@mui/material/ListItemText'
import Avatar from '@mui/material/Avatar'
import { Card, Slider, Stack, TextField } from '@mui/material'
import { useEffect, useState } from 'react'

export default function ProductsList(props) {
  const { products } = props
  function valuetext(value) {
    return `${value}`
  }
  const [element, setElement] = useState(null)
  const [value, setValue] = useState()

  useEffect(() => {
    if (element == null) return
    const [i, j] = element
    setValue(products[i].prices[j].price)
  }, [element])

  useEffect(() => {
    if (element == null || value == null) return
    const [i, j] = element
    products[i].prices[j].price = value
  }, [value])

  return (
    <div>
      <Stack
        sx={{ height: 300, position: `fixed`, right: 0, bottom: 100 }}
        spacing={1}
        direction="row"
      >
        <Slider
          aria-label="Small steps"
          value={value ?? 1}
          onChange={(_, newValue) => setValue(newValue)}
          getAriaValueText={valuetext}
          step={0.1}
          marks
          min={0}
          max={10}
          valueLabelDisplay="auto"
          orientation="vertical"
        />
      </Stack>

      <TextField
        value={value}
        onChange={(event) => setValue(event.target.value)}
        inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
      />
      <List>
        {products?.map(({ product, prices }, index) => (
          <ListItem key={index}>
            <ListItemAvatar>
              <Avatar alt={product} src={product} />
            </ListItemAvatar>
            <ListItemText
              primary={product}
              secondary={showPrices(prices, index)}
            />
          </ListItem>
        ))}
      </List>
    </div>
  )

  function showPrices(prices, i) {
    return prices.map((e, j) => (
      <div
        tabIndex="0"
        className={element?.[0] === i && element?.[1] === j ? 'price' : ``}
        key={j}
        onClick={(event) => {
          setElement([i, j])
          event.target.focus()
        }}
        // onBlur={() => {
        //   setElement(null)
        // }}
      >
        <b>{e.name}: </b> <span>{e.price}</span>
      </div>
    ))
  }
}

