export function ObjGroupBy(list, key) {
  return list.reduce(
    (groups, item) => ({
      ...groups,
      [item[key]]: [...(groups[item[key]] || []), item],
    }),
    {}
  )
}

const LS_DATA_NAME = `selected`

export function lsSave(list) {
  localStorage.setItem(LS_DATA_NAME, JSON.stringify(list))
}

export function lsLoad() {
  const str = localStorage.getItem(LS_DATA_NAME)
  return JSON.parse(str)
}
